
import React from 'react';
import "./footer.scss"
import facebook from '../../assets/images/social/facebook.webp';
import instagram from '../../assets/images/social/instagram.webp';
import pinterest from '../../assets/images/social/pinterest.webp';
import twitter from '../../assets/images/social/twitter.webp';
export default function index() {
    return (
        <div className="footer">
            <div className="social-icons">
                <img src={pinterest} alt="pinterest" />
                <img src={facebook} alt="facebook" />
                <img src={instagram} alt="instagram" />
                <img src={twitter} alt="twitter" />
            </div>
            <p className="copyright">Copyright ©2020 by The Art of Food | Proudly created by TEJA VEJENDLA from <a href="https://speedtechsolutions.github.io">
                <span>Speed Tech Solutions</span>
            </a></p>
        </div>
    )
}
