import React, { Component } from 'react'
import './books.scss'
import Book from './book'
import book1_img from '../../assets/images/books/book (1).webp';
import book2_img from '../../assets/images/books/book (2).webp';
import book3_img from '../../assets/images/books/book (3).webp';

export default function index() {
    const books = [
        {
            title: "BREAKFASTS",
            price: "40",
            imgSrc: book1_img
        },
        {
            title: "SUGAR & SPICE",
            price: "35",
            imgSrc: book2_img
        },
        {
            title: "HOLIDAY SEASON SPECIALS",
            price: "25",
            imgSrc: book3_img
        }
    ];

    return (
        <div className="books">
            {books.map((book, index) => (<Book key={index} book={book}/>))}
        </div>
    )
}

